<template>
  <v-container

    fluid
  >
    <v-row>
      <v-col
        cols="12"
        md="4"
      >
        <v-card
          class="pa-4"
          elevation="0"
        >
          <div class="">
            <div class="display-2 font-weight-light">
              {{ $t('') }}
            </div>
            <v-btn
              class="mt-4"
              block
              elevation="0"
              color="primary"
              @click="returnOrder"
            >
              <span style="font-size: 24px">{{ $t('Storno') }}</span>
            </v-btn>
            <v-btn
              elevation="0"
              block
              class="mt-2"
              color="success"
              @click="reprintDialogShow=true"
            >
              <span style="font-size: 24px">{{ $t('') }}</span>
            </v-btn>
            <v-divider class="my-2"/>
          </div>

          <template v-if="billData.billInfo.paymentStatus==='1'">
            <div
              class="display-2 font-weight-light mt-4"
            >
              {{ $t('') }}
            </div>
            <v-card
              class="mt-2"
              elevation="0"
            >
              <div
                v-for="item in paymentRecordList"
                :key="item.id"
                class="d-flex py-2"
                style="font-size: 36px "
              >
                <div
                  class="primary mr-2 px-2 white--text"
                  style="border-radius: 8px"
                >
                  {{ item.paymentMethodName }}
                </div>
                {{ item.createdAt.split(' ').pop() }}
                <v-spacer/>
                {{ item.amount | priceDisplay }}
              </div>
            </v-card>
            <v-divider class="my-2"/>
          </template>

          <div class="display-2">
            {{ $t('') }}
          </div>
          <v-card
            class="mt-4"
            elevation="0"
          >
            <v-data-table
              :fixed-header="false"
              :headers="infoHeaders"
              :items="infoList"
              :loading="loading"
              :items-per-page="30"
              hide-default-footer
              hide-default-header
            >
              <template v-slot:item.keyname="{ item }">
                <span class="font-weight-black">
                  {{ item.keyname }}
                </span>
              </template>
            </v-data-table>
          </v-card>
          <template v-if="rawAddressInfo">
            <div class="display-2 mt-4 font-weight-light">
              {{ $t('') }}
            </div>
            <v-card class="mt-4">
              <address-info-block :address-info="rawAddressInfo"/>
            </v-card>
          </template>
        </v-card>
      </v-col>
      <v-col
        cols="12"
        md="4"
        class="pa-4"
        style="font-size: 18px"
      >
        <div class="d-flex align-center">
          <div class="display-2">
            {{ $t('meals_detail') }}
          </div>
          <v-spacer/>
          <v-btn-toggle
            v-model="timelineView"
            dense
            mandatory
          >
            <v-btn>
              <v-icon

                left
              >
                mdi-timeline-check-outline
              </v-icon>
              {{ $t('time_line') }}
            </v-btn>
            <v-btn>
              <v-icon
                left
              >
                mdi-format-list-bulleted-type
              </v-icon>
              {{ $t('list') }}
            </v-btn>
          </v-btn-toggle>
        </div>
        <template v-if="timelineView===0">
          <v-timeline
            v-if="basicInfo"
            align-top
            dense
          >
            <v-timeline-item
              color="amber"
              small
            >
              <div class="timeLineGridContainer">
                <strong>{{ basicInfo.startTime | timeStampDisplay }}</strong>
                <div><strong>{{ $t('start_meal') }}</strong></div>
              </div>
            </v-timeline-item>
            <template v-if="displayDetailInfo">
              <template v-for="t in Object.keys(displayDetailInfo)">
                <v-timeline-item
                  :key="t"
                  small
                >
                  <div class="timeLineGridContainer">
                    <strong>{{ t | timeStampDisplay }}</strong>
                    <div>
                      <div
                        v-for="op in Object.keys(displayDetailInfo[t])"
                        :key="t+'op'+op"
                      >
                        <strong>{{ $t(op) }}</strong>
                        <div class="mt-1">
                          <v-card
                            v-for="d in displayDetailInfo[t][op]"
                            :key="d.id"
                            elevation="0"
                            class="pa-1  ml-0 ma-1 text-body-1"
                          >
                            <div>
                              <div class="d-flex">
                                <span>{{ Math.abs(d.count) }}&times; </span>
                                <span class="text-capitalize text-no-wrap text-truncate font-weight-bold"> {{
                                  d.currentCode
                                }} </span>
                                <v-spacer/>
                                <template v-if="parseFloat(d.tempDiscountMod)!==0">
                                  <span
                                    class="text-decoration-line-through warning--text text--darken-1"
                                    style="font-size: 10px"
                                  >{{ d.price | priceDisplay }}</span>
                                  <span class="text-no-wrap">
                                    {{ parseFloat(d.price) + parseFloat(d.tempDiscountMod) | priceDisplay }}</span>
                                </template>
                                <span
                                  v-else
                                  class="text-no-wrap"
                                >{{ d.price | priceDisplay }}</span>
                              </div>

                              <span class="maxLine2">{{ d.name }}</span>
                            </div>
                            <template v-if="d.aName">
                              <div class="text-caption">
                                {{ d.aName }}
                              </div>
                            </template>
                          </v-card>
                        </div>
                      </div>
                    </div>
                  </div>
                </v-timeline-item>
              </template>
            </template>
            <v-timeline-item
              color="success"
              small
            >
              <div class="timeLineGridContainer">
                <strong>{{ basicInfo.endTime | timeStampDisplay }}</strong>
                <div><strong>{{ $t('end_meal') }}</strong></div>
              </div>
            </v-timeline-item>
          </v-timeline>
        </template>
        <template v-else>
          <div class="pa-2">
            <div
              v-for="(d,index) in orders"
              :key="d.id"
            >
              <div>
                <div class="d-flex">
                  <span>{{ Math.abs(d.sumCount) }}&times; </span>
                  <span class="text-capitalize text-no-wrap text-truncate font-weight-bold ml-1">{{ d.code }}</span>
                  <v-spacer/>
                  <template v-if="parseFloat(d.tempDiscountMod)!==0">
                    <span
                      class="text-decoration-line-through
                                warning--text
                                 text--darken-1
                                 align-baseline"
                      style="font-size: 10px"
                    >{{ d.price | priceDisplay }}</span>
                    <span class="text-no-wrap">
                      {{ parseFloat(d.price) + parseFloat(d.tempDiscountMod) | priceDisplay }}</span>
                  </template>
                  <span
                    v-else
                    class="text-no-wrap"
                  >{{ d.price | priceDisplay }}</span>
                </div>

                <span class="maxLine2">{{ d.name }}</span>
              </div>
              <template v-if="d.aName">
                <div class="text-caption">
                  {{ d.aName }}
                </div>
              </template>
              <v-divider
                v-if="index!=orders.length-1"
                class="my-2"
              />
            </div>
          </div>
        </template>
      </v-col>
      <v-col
        id="billPayment"
        cols="4"
      >
        <v-card class="pa-4">
          <div class="display-2 font-weight-light mt-4">
            {{ $t('') }}
          </div>
          <v-card
            class="mt-4"
            elevation="0"
          >
            <v-list>
              <v-list-item
                v-for="item in printRecordList"
                :key="item.id"
                @click="showContentOf(item.content)"
              >
                <v-list-item-content>
                  <v-list-item-title>
                    {{ item.title }} {{ item.hasPrint === '1' ? $t('') : $t('') }}
                  </v-list-item-title>
                  <v-list-item-subtitle>
                    {{ item.printTimeStamp.split(' ').pop() }}/ {{
                      item.printerMsg
                    }}
                  </v-list-item-subtitle>
                </v-list-item-content>
                <v-list-item-action>
                  <v-icon
                    class="mr-2"
                    small
                    @click.stop="reprint(item)"
                  >
                    mdi-printer
                  </v-icon>
                </v-list-item-action>
              </v-list-item>
            </v-list>
          </v-card>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog
      v-model="printContentShow"
      width="480px"
    >
      <v-card
        width="100%"
        class="pa-4"
      >
        <i-k-p-r-node
          style="font-family: monospace"
          level=""
          :node="parseNode(currentPrintContent)"
        />
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="reprintDialogShow"
      max-width="300px"
    >
      <v-card>
        <v-card-title>{{ $t('select_bill_type') }}</v-card-title>
        <v-card-text>
          <v-btn
            class="pa-2"
            style="width: 100%"
            @click="reprintOrder(1)"
          >
            {{
              $t('Normal Rechnung')

            }}
          </v-btn>
          <v-btn
            class="pa-2"
            style="width: 100%"
            @click="reprintOrder(2)"
          >
            {{

              $t('Unternehmensrechnung-1')

            }}
          </v-btn>
          <v-btn
            class="pa-2"
            style="width: 100%"
            @click="reprintOrder(3)"
          >
            {{

              $t('Unternehmensrechnung-2')
            }}
          </v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  import { getDetailOrder, reprintOrder } from '@/components/Utlis/api'
  import i18n from '../../i18n'
  import AddressInfoBlock from '../../components/base/AddressInfoBlock'
  import BillPaymentInfo from '../../model/BillPaymentInfo'
  import IKUtils from 'innerken-js-utils'
  import Payment from '../../model/Payment'
  import ConsumeTypeStatus from '../../model/ConsumeTypeStatus'
  import PrinterListByOrder, { getOneDishOrderRecords } from '../../model/PrinterListByOrder'
  import hillo from 'hillo'
  import ConsumeType from '../../model/ConsumeType'
  import { groupBy } from 'lodash'
  import IKPRNode from '@/components/IKPRNode'
  import { parse } from 'node-html-parser'

  export default {
    components: {
      IKPRNode,
      AddressInfoBlock,
    },
    props: {
      id: {
        default: '-1',
        type: [String, Number],
      },
    },
    data: function () {
      return {
        currentPrintContent: '',
        printContentShow: null,
        timelineView: 0,
        reprintDialogShow: null,
        items: [
          this.$i18n.t(''),
          this.$i18n.t('')],
        newId: null,
        newAmount: null,
        BillPaymentInfo: BillPaymentInfo,
        loading: false,
        billData: {
          billInfo: { paymentStatus: 0 },
          billOrderInfo: [],
          billPaymentInfo: [],
        },
        printDialogShow: false,
        newPaymentLog: [],
        payMethods: [],

        rawAddressInfo: '',
        infoHeaders: [
          {
            value: 'keyname',
            align: 'left',
          },
          {
            value: 'value',
            align: 'right',
          },
        ],
        infoList: [],
        headers: [
          {
            text: this.$i18n.t('code'),
            align: 'start',
            sortable: false,
            value: 'code',
          },
          {
            text: this.$i18n.t('name'),
            value: 'name',
          },
          {
            text: this.$i18n.t('modification'),
            value: 'mod',
          },
          {
            text: this.$i18n.t('createTimestamp'),
            value: 'stamp',
          },
          {
            text: this.$i18n.t('consumeTypeId'),
            value: 'consumeTypeName',
          },
          {
            text: this.$i18n.t('price'),
            value: 'price',
            align: 'center',
          },
          {
            text: this.$i18n.t('count'),
            value: 'sumCount',
            align: 'center',
          },
          {
            text: this.$i18n.t('sumPrice'),
            value: 'totalPrice',
            align: 'center',
          },
          {
            text: this.$i18n.t(''),
            value: 'note',
          },
          {
            text: this.$i18n.t('usageStatus'),
            value: 'dishStatusString',
            align: 'center',
          }],
        orders: [],
        defaultItem: {
          paymentMethodId: -1,
          name: '',
        },
        consumeTypeStatusList: [],
        consumeTypeStatus: '',
        paymentStatus: '',
        paymentRecordList: [],
        printRecordList: [],
        PrinterListByOrder: PrinterListByOrder,
      }
    },
    computed: {
      lineList () {
        const node = this.currentPrintContent.replace('<br>', '\n')
          .replace('<BR>', '\n').split('\n')
        console.log(node)
        return node
      },
      basicInfo () {
        return this.billData?.billInfo
      },
      totalPrice () {
        return parseFloat(this.billData.billInfo.totalPrice ?? 0)
      },
      detailedOrders () {
        return (this.billData?.orderDetailInfo ?? []).map(o => {
          let name
          switch (o.dishStatus) {
            case '-1':
              name = 'returndish'
              break
            case '-100':
              name = 'divided_order'
              break
            default:
              name = 'order_food'
          }
          o.dishStatusName = name
          return o
        })
      },
      displayDetailInfo () {
        const groupedInfo = groupBy(this.detailedOrders, 'time')
        for (const time in groupedInfo) {
          groupedInfo[time] = groupBy(groupedInfo[time], 'dishStatusName')
          for (const key in groupedInfo[time]) {
            const timeInfo = groupBy(groupedInfo[time][key], (i) => {
              return i.currentCode + i.name + i.note + i.aName + i.tempDiscountStr
            })
            groupedInfo[time][key] = Object.keys(timeInfo).reduce((arr, i) => {
              arr.push(timeInfo[i].reduce((obj, t) => {
                obj.count = parseInt(obj.count) + parseInt(t.count)
                return obj
              }))
              return arr
            }, [])
          }
        }
        return groupedInfo
      },
    },
    watch: {
      id: {
        immediate: true,
        handler: function () {
          this.reloadItem(this.id)
        },
      },
    },
    mounted () {

    },
    methods: {
      parseNode (node) {
        return parse(node)
      },
      showContentOf (content) {
        this.currentPrintContent = content
        this.printContentShow = true
      },
      formatItem () {
        for (const i of this.payMethods) {
          i.name = this.findLangInArray(i.langs).name
        }
      },
      loadDetail: function (id) {
        getDetailOrder(id).then(async res => {
          this.billData = res
          const list = await ConsumeType.getList()

          if (this.billData.billOrderInfo) {
            this.orders = this.billData.billOrderInfo.map((item, index) => ({
              id: index,
              consumeTypeName:
                item.overrideConsumeTypeId
                  ? list.find(i => i.id === parseInt(item.overrideConsumeTypeId))?.name : '',
              ...item,
            }))
          } else {
            this.orders = this.billData.orderDetailInfo.map((item) => ({
              id: item.id,
              sumCount: item.count,
              code: item.currentCode,
              name: item.name,
              note: item.note,
              aName: item.aName,
              stamp: item.time,
              consumeTypeName: '',
              totalPrice: item.price,
            }))
          }

          this.rawAddressInfo = JSON.parse(this.billData.billInfo.rawAddressInfo)
          console.log('billDetailpage ', this.rawAddressInfo)
          this.paymentStatus = this.billData.billInfo.paymentStatus === '1' ? this.$i18n.t('') : this.$i18n.t('')
          this.consumeTypeStatus = this.consumeTypeStatusList.find(i =>
            parseInt(this.billData.billInfo.consumeTypeStatusId) === i.id).name
          console.log(this.billData.billInfo.payMethod, 'this.defaultItem.name')
          for (const item of this.payMethods) {
            if (parseInt(item.id) === parseInt(this.billData.billInfo.payMethod)) {
              this.defaultItem.name = item.name
            }
          }
          this.infoList = [
            {
              keyname: this.$i18n.t('') + '/' + this.$i18n.t('tableName'),
              value: this.billData.billInfo.id + '/' + this.billData.billInfo.tableName,
            },
            {
              keyname: this.$i18n.t('servantName'),
              value: this.billData.billInfo.servantName,
            },
            {
              keyname: this.$i18n.t('startTime'),
              value: this.billData.billInfo.startTime,
            },
            {
              keyname: this.$i18n.t(''),
              value: this.billData.billInfo.endTime,
            },
            {
              keyname: this.$i18n.t('payMethod') + '/' + this.$i18n.t(''),
              value: this.defaultItem.name + '/' + this.paymentStatus,
            },
            {
              keyname: this.$i18n.t('tipIncome'),
              value: this.billData.billInfo.tipIncome,
            },
            {
              keyname: this.$i18n.t('discount'),
              value: this.billData.billInfo.discountStr,
            },
            {
              keyname: this.$i18n.t('totalPrice'),
              value: this.billData.billInfo.totalPrice,
            },
          ]
          this.paymentRecordList = res?.billPaymentInfo.map(item => {
            item.paymentMethodName = this.findPaymentName(item.paymentMethodId)
            return item
          }) ?? []
          this.printRecordList = await getOneDishOrderRecords(this.id)
          console.log(this.printRecordList)
        })
      },
      findPaymentName (id) {
        return this.payMethods.find(it => parseInt(it.id) === parseInt(id)).name
      },
      async reloadItem (id) {
        this.consumeTypeStatusList = await ConsumeTypeStatus.getList()
        this.payMethods = (await Payment.getList())
        this.formatItem()
        this.loadDetail(id)
      },
      async returnOrder () {
        IKUtils.showLoading()
        await hillo.post('Complex.php?op=returnOrder', { orderId: this.id })
        IKUtils.hideLoading()
      },
      async reprintOrder (value) {
        value = parseInt(value)
        let withTitle = 0
        let printCount = 1
        if (value > 1) {
          withTitle = 1
        }
        if (value > 2) {
          printCount = 2
        }
        await reprintOrder(this.id, withTitle, printCount)
        this.reprintDialogShow = false
      },
      findLangInArray (langArray, target = i18n.locale) {
        return langArray.find((item) => {
          return item.lang.toLowerCase() === target.toLowerCase()
        })
      },
      async reprint (item) {
        await this.PrinterListByOrder.reprint(item)
        IKUtils.toast()
      },
    },
  }
</script>

<style scoped>

</style>
